.header {
  transition: background-position $transition-time ease;
  background-image: url(/images/Cover_photo.png);
  background-size: cover;
  background-position: 88%;

  @include onTablet {
    background-position: center;
  }

  &__content {

    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0;
    
    text-align: center;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 64px;
    }

    @include onDesctop {
      font-size: 80px;
    }

  }

  &__bottom {
    padding-bottom: 88px;

    @include onTablet {
      padding-bottom: 130px;
    }

    @include onDesctop {
      padding-bottom: 203px;
    }

  }

}
